<template>
<div>
    <router-link to="/keychain"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
        <div class="layout-body">
            <div class="jumbo">
                <p class="container-title"><b>My devices</b></p>
            </div>

            <div class="section-form device-container">
                <div class="list-group">
                    <a v-for="device in devices" :key="device.id" v-if="device.existing_status === 'active'" class="list-group-item list-group-item-action flex-column align-items-start havr-list">

    <div class="d-flex  justify-content-between havr-list-device">
     
      <h5 class="mb-1 black-havr-text">  <img class="havr-device-icon" v-if="device.device_type === 'ios'" src="@/assets/havr-icon/apple-icon.svg"> 
       <img class="havr-device-icon" v-else src="@/assets/havr-icon/android-icon.svg">
      {{device.name}}</h5>
      <small><img @click="currentDeviceId = device.id, showPasswordModal = !showPasswordModal" class="delete-device" src="@/assets/havr-icon/delete.svg"></small>
      
    </div>
    <small class="havr-device-subtext">{{device.humanized_date}}</small>
  </a>
                </div>
            </div>
        </div>
        <b-modal :hide-footer="true" :ok-title="'Save'" :hide-header="true" :ok-variant="'havr-black-color'" centered v-model="showPasswordModal">
                    <div class="form-group">
                        <label>Please enter your password to confirm your action</label>
                        <input v-model="password" type="password" class="form-control havr-black-input">
  </div>
   <button @click="deleteDevice" class="btn btn-primary havr-black-color modal-button" >Delete</button>
                            <button @click="showPasswordModal = !showPasswordModal" class="btn btn-primary havr-black-inversed" >Cancel</button>
              
                </b-modal>

</div>
</template>

<script>
import bModal from "bootstrap-vue/es/components/modal/modal";
import notifiy from "vue-notification";
import repository from "@/repository/misc";
import moment from 'moment'
export default {
  data() {
    return {
      devices: null,
      currentDeviceId: null,
      password: null,
      showPasswordModal: false
    };
  },
   components: {
    "b-modal": bModal,
   },
  mounted() {
    this.getDevices();
  },
  methods: {
    /**
     * get all devices of the users
     */
    async getDevices() {
      const res = await repository.getDevices();
      if (res) {
          const data = res.map(i => ({...i, humanized_date : 'Added on ' + moment(i.created_at).format('LL')}))
          this.devices = data;
      }
    },
    /**
     * delete device of a user
     */
    async deleteDevice() {
      const password = {password: this.password}
      const res = await repository.deleteDevice(this.currentDeviceId,password);
      if (res) { 
        this.notify("success", "Device deleted with success !");
        this.getDevices();
        this.showPasswordModal = !this.showPasswordModal
      } else this.notify("danger", "Internal Error");
    },
    /**
     * Notify wrapper
     */
    notify(type, message) {
      this.$notify({
        type: type,
        message: message,
        timeout: 2000
      });
    }
  }
};
</script>

